


.pwd {
    position: relative;
    width: 2rem;
   
}
.user {
    width:2rem;
}
.list {
    width: 2rem;
    margin-top:0.3rem; 
}
#s-btn{
    width: 100px;
    height: 30px;
    position: absolute;
    right: 0;
    bottom: 5px;
}
.l-btn {
    width: 2rem;
}
.wholeLogin {

    width: 100%;
    height: 100%;
}
.up {
    width: 100%;
    height: 50%;
    background: #444;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 60px;
}
.down {
    width: 100%;
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login-form-button{
    color: #fff;
    background: skyblue;
    border: 0;
    width: 1.6rem;
    height: 0.25rem;
    border-radius: 0.25rem;
}